<template>
  <div class="tab-control">
    <div v-for="(item,index) in titles" 
    :class="{active: index === currentIndex}"
    :key="item.res"
    @click="itemClick(index)"
    >
      <span >{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  props: {
    titles: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  methods: {
    itemClick(index){
      this.currentIndex = index
    }
  }
};
</script>

<style scoped>
.tab-control {
  display: flex;
  text-align: center;
  line-height: 40px;
  height: 40px;
}
.tab-control div {
  flex: 1;
}
.active span{
  padding-bottom: 5px;
  border-bottom: 4px solid red;
}
</style>